@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  background-color: #332c50;
  color: #e2f3e4;
}

.code-container {
  height: 100%;
}

.code-container__editor {
  width: 100%;
  height: 100%;
  line-height: 1.5;
}

.app {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  -webkit-flex-direction: 'row';
          flex-direction: 'row';
  font-family: 'Press Start 2P';
}

.labs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.projects {
  margin-top: 5%;
  margin-bottom: 5%;
  margin-right: 10%;
  padding: 2.4%;
  text-align: center;
  margin-left: 10%;
  background-color: #332431;
  border-style: groove;
  border-width: medium;
  border-color: #e2f3e4;
  font-family: 'Press Start 2P';
  color: #e2f3e4;
  font-size: 1em;

}

.labs__title {
  font-size: 3em;
  text-align: center;
  margin-top: 4%;
}

.home__title {
  font-size: 4em;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 2%;
  margin-top: 5%;
  width: 40%;
  border: groove;
  margin-right:auto;
  margin-left:auto;
  background-color: #332431;
  border-width: medium;
}

.text__block {
  width: 40%;
  font-size: 1.1em;
  text-align: left;
  padding-left: 7%;
  line-height: 200%;
  margin-top: 7.5%;
  float:left;
}

img {
  float: right;
  margin-right: 10%;
  margin-top: 4%;
  width: 35%;
  height: auto;
  image-rendering: -moz-crisp-edges;
  image-rendering: pixelated;
}

.play {
    display: -webkit-flex;
    display: flex;
    height: 100vh;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.play .canvas {
    width: 512px;
    height: 512px;
    background-color: #332431;
}
.topbar {
    background-color: #303042;
    width: 100%;
}

.topbar__list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    list-style-type: none;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex: 9 1;
            flex: 9 1;

}

.topbar__list li {
    padding-top: 0.85em;
    padding-bottom: 0.85em;
    padding-right: 1.3em;
    padding-left: 1.3em;
}

.topbar__list li a {
    text-decoration: none;
    color: #e2f3e4;
}

.topbar__list li a.selected {
    color: #54546a;
}

.logged-in {
    height: 100%;
}
.app {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
/* Syntax highlighting */
.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #655f7e;
}
.token.punctuation {
  color: #9e9e9e;
}
.namespace {
  opacity: 0.7;
}
.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #94e344;
}
.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #4caf50;
}
.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #46878f;
}
.token.atrule,
.token.attr-value,
.token.keyword {
  color: #3f51b5;
}
.token.function {
  color: #e2f3e4;
}
.token.regex,
.token.important,
.token.variable {
  color: #ff9800;
}
.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}
.token.entity {
  cursor: help;
}

